import Title from '../components/Title'

// Query the database for a user image here

state = {}

uploadFile = async e => {
  console.log('uploading file')
  const files = e.target.files
  const data = new FormData()
  data.append('file', files[0])
  data.append('upload_preset', 'artiststore')

  const res = await fetch('https://api.cloudinary.com/v1_1/y8s/image/upload', {
    method: 'POST',
    body: data
  })
  const file = await res.json()
  console.log(file)
  this.setState({
    image: file.secure_url
  })
  const hero = document.querySelector('#hero')
  if (this.state.image !== '') {
    hero.src = this.state.image
    hero.classList.remove('two-hundred')
  }
}

const about = props => {
  return (
    <>
      <Title title="About" />
      <h3 className="fw-500">Details</h3>
      <p>
        Wow. Some cool information about the store. Wow. Even more. Wow. Some
        cool information about the store. Wow. Even more. Wow. Some cool
        information about the store. Wow. Even more. Wow. Some cool information
        about the store. Wow. Even more.
      </p>

      <label htmlFor="userImage">
        <input type="file" name="userImage" id="userImage" />
      </label>
    </>
  )
}

export default about
